.card {
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: #999a9d;
  padding: 5px;
  z-index: 5;
  box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  width: 1160px;
  min-height: 560px;
  box-sizing: border-box;
  transition: all 200ms ease-in-out;
}
.card-title {
  display: flex;
  flex-direction: row-reverse;
  color: white;
  font-size: 20px;
  border-radius: 20px;
}

.card-contact {
  display: flex;
  justify-content: center;
  text-align: right;
  padding: 3px;
}

.contact-text {
  display: flex;
  flex: 1;
  flex-direction: column;
  font-size: 13px;
}
.contact-gallery {
  display: flex;
  flex: 1;
}

.gallery-images {
  display: flex;
  position: absolute;
  overflow-x: hidden;
  width: 610px;
  margin-top: 14px;
  left: -30px;
  border-radius: 1%;
}

.gallery-image img {
  margin: 8px;
  width: 250px;
  height: 400px;
  object-fit: cover;
  object-position: center;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.3);
}

.gallery-scrollbar {
  display: flex;
  width: 100%;
}

@media screen and (max-width: 1240px) {
  .card {
    width: 900px;
    min-height: 560px;
  }
}

@media screen and (max-width: 1000px) {
  .card {
    width: 600px;
    min-height: 760px;
  }
  .card-contact {
    flex-direction: column;
    padding: 0;
    margin: 0;
  }
  .contact-text {
    
    padding-top: 410px;
  }
  .gallery-images {
    left: 2px;
    margin: 0;
    width: 596px;

  }
}
